// import logo from "./logo.svg";
import "./App.css";
import { useState, useEffect, useRef } from "react";

function App() {
  const [src, setSrc] = useState(null);

  useEffect(() => {
    navigator.mediaDevices
      .getUserMedia({
        video: {
          width: { min: 1280 },
          height: { min: 720 },
        },
      })
      .then((stream) => {
        setSrc(stream);
      });
  }, []);
  return (
    <div className="App">
      <video
        autoPlay
        ref={(video) => {
          if (!!src) {
            video.srcObject = src;
          }
        }}
        style={{ width: "1000px", height: "500px", backgroundColor: "#343434" }}
      />
    </div>
  );
}

export default App;
